
.testimonials {
    font-family: "Karla", serif;
    background-color: #333333;
    padding: 7rem 2rem 12rem 2rem;
    transition-duration: .2s;
}

.testimonials h3 {
    font-size: 2rem;
    font-weight: 700;
    color: white;
    text-align: center;
}

.testimonials section {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 1rem;
}

.testimonials .card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #EDEFEE;
    padding: 1rem;
    width: 250px;
    height: 200px;
    border-radius: 8px;
    box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.195);
}

.testimonials .card > div {
    display: flex;
    align-items: center;
}

.testimonials .card p {
    position: relative;
    top: .5rem;
    font-weight: 700;
    margin: .5rem;
}

.testimonials .card img {
    width: 100px;
    height: 100px;
    border-radius: 10%;
    object-fit: cover;
}

.testimonials .card span {
    font-size: .8rem;
    color: #333333;
}

@media all and (min-width: 1000px) {
    .testimonials {
        padding: 7rem 12rem 12rem;
        transition-duration: .2s;
    }
}
